  // border: 0.2px solid black !important;
}
#root {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
  position: relative;
  background-color: #f7f7f7;
}
.container {
  max-width: 1263px;
  margin: 0 auto;
}
.scroll {
  overflow: hidden;
  overflow: scroll;
  scrollbar-width: none;
  &::-webkit-scrollbar {
    display: none;
  }
}
